import Image from "next/image";

export default function Home() {
  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-900 via-gray-800 to-gray-900 text-white">
      {/* HERO SECTION */}
      <section className="px-6 md:px-16 py-20 text-center">
        <h1 className="text-4xl md:text-6xl font-extrabold leading-tight">
          Find Your Next <span className="text-blue-500">Event</span>
        </h1>
        <p className="mt-4 text-gray-300 text-lg md:text-xl max-w-2xl mx-auto">
          Discover sports, concerts, and live shows happening around you.
        </p>

        {/* SEARCH BAR */}
        <div className="mt-10 max-w-3xl mx-auto">
          <input
            type="text"
            placeholder="Search for events, teams, or venues..."
            className="w-full px-6 py-4 rounded-xl bg-gray-800 border border-gray-700 text-white focus:ring-2 focus:ring-blue-500 outline-none"
          />
        </div>
      </section>

      {/* CATEGORY CARDS */}
      <section className="px-6 md:px-16 py-14">
        <h2 className="text-3xl font-bold mb-8">Popular Categories</h2>

        <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
          {[
            { name: "Sports", img: "/images/sports.jpg" },
            { name: "Concerts", img: "/images/concerts.jpg" },
            { name: "Theatre", img: "/images/theatre.jpg" },
            { name: "Festivals", img: "/images/festival.jpg" },
          ].map((item, index) => (
            <div
              key={index}
              className="relative rounded-xl overflow-hidden group cursor-pointer shadow-lg"
            >
              <Image
                src={item.img}
                width={400}
                height={300}
                alt={item.name}
                className="group-hover:scale-110 transition duration-500"
              />

              <div className="absolute inset-0 bg-black/40 flex items-center justify-center text-xl font-bold">
                {item.name}
              </div>
            </div>
          ))}
        </div>
      </section>

      {/* TRENDING EVENTS */}
      <section className="px-6 md:px-16 py-14">
        <h2 className="text-3xl font-bold mb-6">Trending Events</h2>

        <div className="grid md:grid-cols-3 gap-8">
          {[1, 2, 3].map((event) => (
            <div
              key={event}
              className="bg-gray-800 rounded-xl p-5 shadow-lg hover:shadow-blue-500/20 transition"
            >
              <div className="h-48 bg-gray-700 rounded-lg mb-4"></div>

              <h3 className="text-xl font-semibold">Event Title {event}</h3>
              <p className="text-gray-400 mt-1">Location Â· Date</p>

              <button className="mt-4 w-full py-2 bg-blue-600 hover:bg-blue-500 rounded-lg font-medium">
                View Tickets
              </button>
            </div>
          ))}
        </div>
      </section>

      {/* CTA SECTION */}
      <section className="px-6 md:px-16 py-16 text-center bg-gray-800">
        <h2 className="text-3xl md:text-4xl font-bold">
          Never Miss an Event Again
        </h2>
        <p className="text-gray-400 mt-2">
          Subscribe for updates on your favorite teams and artists.
        </p>

        <button className="mt-6 px-8 py-3 bg-blue-600 hover:bg-blue-500 rounded-lg text-lg font-medium">
          Subscribe Now
        </button>
      </section>
    </div>
  );
}
